  box-sizing: border-box; }

html,
body {
  font-style: normal;
  line-height: 167%;
  color: #484848;
  font-family: "FiraSans", "Arial", "sans-serif";
  font-weight: 400;
  font-size: 15px; }

* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0); }

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%; }

body {
  min-height: 100vh;
  overflow: auto;
  background-color: #ffffff; }

img,
video,
svg {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

/* :focus {
  outline: none;
} */
input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"], [type="number"], [type="password"], [type="search"], [type="tel"], [type="text"], [type="url"]) {
  appearance: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.container {
  max-width: 1366px;
  margin: 32px auto; }
  @media (max-width: 768px) {
    .container {
      max-width: 768px;
      margin: 15px auto; } }

.main {
  height: 906px; }
  .main__bg {
    position: relative;
    background-repeat: no-repeat;
    background-position-y: -30px;
    background-position-x: center;
    display: block; }
    @media (max-width: 1024px) {
      .main__bg {
        background-size: 1024px auto;
        background-position-y: 0; } }
    @media (max-width: 768px) {
      .main__bg {
        background-size: 880px;
        background-position-y: 20px; } }
  .main__body {
    position: relative;
    height: 906px; }
  .main__content {
    max-width: 343px;
    text-align: center;
    display: flex;
    flex-flow: column;
    align-items: center;
    padding: 435px 0 0 0;
    margin: 0 auto; }
    @media (max-width: 1024px) {
      .main__content {
        padding-top: 300px; } }
    @media (max-width: 768px) {
      .main__content {
        padding-top: 245px; } }
  .main__sub-title {
    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
    color: #ffffff;
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    margin-bottom: 35px; }
    @media (max-width: 768px) {
      .main__sub-title {
        margin-bottom: 20px;
        font-size: 20px; } }
  .main__scroll-link {
    transition: ease-in-out 0.2s;
    cursor: pointer; }
    .main__scroll-link:hover {
      transform: scale(1.1); }
  .main__scroll-img {
    background-repeat: no-repeat;
    background-position-x: 29px;
    background-position-y: 25px; }

body.webp .main__bg {
  background-image: url("../img/bg.webp"); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    body.webp .main__bg {
      background-image: url("../img/bg@2.webp"); } }
  @media (max-width: 768px) {
    body.webp .main__bg {
      background-image: url("../img/bg_mobile.webp"); } }
  @media (max-width: 768px) and (min-resolution: 144dpi), (max-width: 768px) and (min-resolution: 1.5dppx) {
    body.webp .main__bg {
      background-image: url("../img/bg_mobile@2.webp"); } }

body.no-webp .main__bg {
  background-image: url("../img/bg.jpg"); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    body.no-webp .main__bg {
      background-image: url("../img/bg@2.jpg"); } }
  @media (max-width: 768px) {
    body.no-webp .main__bg {
      background-image: url("../img/bg_mobile.jpg"); } }
  @media (max-width: 768px) and (min-resolution: 144dpi), (max-width: 768px) and (min-resolution: 1.5dppx) {
    body.no-webp .main__bg {
      background-image: url("../img/bg_mobile@2.jpg"); } }

body.webp .main__scroll-img {
  background-image: url("../img/mouse.webp"); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    body.webp .main__scroll-img {
      background-image: url("../img/mouse@2.webp"); } }

body.no-webp .main__scroll-img {
  background-image: url("../img/mouse.png"); }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    body.no-webp .main__scroll-img {
      background-image: url("../img/mouse@2.png"); } }

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 110px;
  z-index: 1;
  position: relative;
  height: 52px; }
  @media (max-width: 768px) {
    .menu {
      padding: 0 13px;
      flex-flow: column wrap;
      align-items: space-between;
      justify-content: start; } }
  @media (max-width: 320px) {
    .menu {
      padding: 0; } }
  .menu__number {
    font-weight: 500;
    text-decoration: none;
    color: #484848;
    white-space: nowrap;
    transition: ease-in-out 0.2s; }
    .menu__number:hover {
      color: black;
      transform: scale(1.05); }
    @media (max-width: 768px) {
      .menu__number {
        order: 2;
        font-size: 14px; } }
  .menu__logo-link {
    cursor: pointer; }
  .menu__logo {
    width: 137px;
    height: 48px; }
    @media (max-width: 768px) {
      .menu__logo {
        width: 120px;
        height: 42px;
        margin-right: 30px; } }
  .menu__ring-me {
    border: none;
    height: 22px;
    text-decoration: none;
    font-size: 15px;
    font-weight: 500;
    color: #484848;
    font-family: "FiraSans", "Arial", "sans-serif";
    border-bottom: 1px dashed #484848;
    text-transform: uppercase;
    cursor: pointer;
    white-space: nowrap;
    transition: ease-in-out 0.2s; }
    .menu__ring-me:hover {
      color: black;
      transform: scale(1.05); }
    @media (max-width: 768px) {
      .menu__ring-me {
        order: 3;
        font-size: 14px;
        text-transform: none;
        margin-left: 15px; } }

